export const useSubjectTabManage = (args) => {
  const { data, refetch, isFetching } = useSubjectTabManageQuery(args.data, {
    skip: args.skip,
  });
  return {
    subjectTabManage: data,
    subjectTabManageRefetch: refetch,
    subjectTabManageLoading: isFetching,
  };
};

